.light-box {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 300;
  pointer-events: none;
  opacity: 0;
  transition: all 600ms ease;
  &.active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.6);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__inner {
    width: 492px;
    height: 285px;
    background: transparent url('../images/lightbox-bg@2x.png') no-repeat 0 0;
    background-size: cover;
    position: relative;
  }
  &__text {
    width: 387px;
    height: 37px;
    padding-top: 74px;
    margin: 0 auto 40px;
  }
  &__btn-close {
    width: 26px;
    height: 27px;
    position: absolute;
    right: 30px;
    top: 30px;
    border: 0;
    padding: 0;
    background-color: transparent;
  }
  &__btn-confirm {
    background-color: transparent;
    width: 163px;
    height: 62px;
    display: block;
    margin: 0 auto;
    border: 0;
    padding: 0;
  }
}