.game-class {
  &__title {
    width: 613px;
    height: 114px;
    margin: 0 auto 20px;
  }
  &__nav {
    position: relative;
    text-align: center;
    font-size: 0;
    margin: 0 auto 44px;
  }
  &__btn {
    display: inline-block;
    width: 268px;
    height: 81.5px;
    background: transparent url('../images//game-class-nav-bg.png') no-repeat 0 0;
    background-size: 268px auto;
    font-size: 30px;
    color: #301c09;
    line-height: 76px;
    text-align: center;
    position: relative;
    &:hover {
      background-position: 0 -81.5px;
    }
    &.active {
      background-position: 0 -81.5px;
    }
    &--knight {
      z-index: 30;
    }
    &--support {
      z-index: 20;
      margin-left: -22px;
    }
    &--system {
      z-index: 10;
      margin-left: -22px;
    }
  }
  &__sub-title {
    width: auto;
    height: 111px;
    margin: 0 auto 72px;
  }
  &__wrap {
    width: 768px;
    height: 1180px;
    position: relative;
  }
  &__page {
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    pointer-events: none;
    transition: opacity 500ms ease;
    &.active {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__slider-wrap {
    width: 768px;
    height: 1180px;
    background: transparent url('../images/game-class-bg@2x.png') no-repeat 0 bottom;
    background-size: 768px 1016px;
    position: relative;
    .swiper-slide {
      .rsp-img {
        width: 733px;
        height: 978px;
        margin: 0 auto;
      }
    }
    .swiper-pagination {
      bottom: 70px;
    }
    .swiper-pagination-bullet {
      width: 70px;
      height: 11px;
      border-radius: 0;
      background: transparent url('../images/pagination-bg.png') no-repeat 0 0;
      opacity: 1;
    }
    .swiper-pagination-bullet-active {
      background-position: 0 -11px;
    }
    .cus-knight-swiper-button-next,
    .cus-support-swiper-button-next,
    .cus-system-swiper-button-next {
      width: 56px;
      height: 77px;
      background: transparent url('../images/btn-next@2x.png') no-repeat 0 0;
      background-size: 56px 77px;
      right: 0;
      top: 52%;
      position: absolute;
      cursor: pointer;
      z-index: 300;
      &.swiper-button-disabled {
        opacity: 0.5;
        pointer-events: none;
      }
      &:focus {
        outline: 0;
      }
      &::after {
        display: none;
      }
    }
    .cus-knight-swiper-button-prev,
    .cus-support-swiper-button-prev,
    .cus-system-swiper-button-prev {
      width: 56px;
      height: 77px;
      background: transparent url('../images/btn-prev@2x.png') no-repeat 0 0;
      background-size: 56px 77px;
      left: 0;
      top: 52%;
      position: absolute;
      cursor: pointer;
      z-index: 300;
      &.swiper-button-disabled {
        opacity: 0.5;
        pointer-events: none;
      }
      &:focus {
        outline: 0;
      }
      &::after {
        display: none;
      }
    }
  }
}

.knight-slide {
  width: 733px;
  height: 1180px;
  margin: 0 auto;
  position: relative;
}