.home {
  overflow: hidden;
  height: 955px;
  position: relative;
  &__logo {
    width: 88px;
    height: 82px;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    padding-top: 5px;
    padding-left: 7px;
    position: relative;
    z-index: 50;
    padding-right: 10px;
  }
  &__nav {
    display: flex;
    align-items: center;
  }
  &__btn-link {
    width: 60px;
    height: 60px;
    margin-right: 10px;
  }
  &__btn-toggle {
    width: 76px;
    height: 64px;
    background-color: transparent;
    border: 0;
    padding: 0;
    display: none;
    @include susy-breakpoint($s-320, $c8) {
      display: block;
    }
  }
  &__main-title {
    width: 771px;
    height: 299px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-8%,0);
    animation-name: mainTitleAni;
    animation-duration: 8000ms;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
  }
  &__sub-title {
    width: 738px;
    height: 104px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,263%,0);
  }
  &__lens-flare {
    background: transparent url('../images/lens-flare.png') no-repeat 0 0;
    width: 344px;
    height: 280px;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    animation-name: opacityAni;
    animation-duration: 3000ms;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    &--1 {
      transform: translate3d(-141%,-76%,0);
      animation-delay: 0;
    }
    &--2 {
      transform: translate3d(-98%,-53%,0);
      animation-delay: 300ms;
    }
    &--3 {
      transform: translate3d(-41%,-77%,0);
      animation-delay: 600ms;
    }
    &--4 {
      transform: translate3d(10%,-26%,0);
      animation-delay: 900ms;
    }
  }
  &__btn-close {
    width: 76px;
    height: 64px;
    background: transparent url('../images/btn-toggle-on.png') no-repeat 0 0;
    background-size: 76px 64px;
    position: absolute;
    right: 10px;
    top: 14px;
    border: 0;
  }
  &__mobile-nav {
    box-sizing: border-box;
    padding-top: 109px;
    width: 328px;
    height: 708px;
    background-color: #000;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 0;
    opacity: 0;
    pointer-events: none;
    transition: all 500ms ease;
    &.active {
      opacity: 1;
      pointer-events: auto;
    }
    a {
      text-align: center;
      font-size: 32px;
      color: #edcb90;
      display: block;
      margin-bottom: 67px;
      transition: all 300ms ease-in-out;
      &:hover {
        color: #fff;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

@keyframes opacityAni {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

@keyframes mainTitleAni {
  0% {
    transform: translate3d(-50%,-8%,0);
  }
  30% {
    transform: translate3d(-50%,-6%,0);
  }
  60% {
    transform: translate3d(-50%,-10%,0);
  }
  100% {
    transform: translate3d(-50%,-8%,0);
  }
}