.reservation {
  background: transparent url('../images/reservation-bg@2x.png') no-repeat center top;
  background-size: cover;
  width: 768px;
  height: 920px;
  margin: 0 auto 136px;
  box-sizing: border-box;
  padding-top: 50px;
  &__nav {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
  }
  &__btn-link {
    display: block;
    font-size: 0;
    width: 356px;
    height: 130px;
    background: transparent url('../images/reservation-nav-bg.png') no-repeat 0 0;
    background-size: 712px auto;
    &--reservation {
      background-position: 0 0;
      &:hover {
        background-position: 0 -130px;
      }
    }
    &--page {
      background-position: -356px 0;
      &:hover {
        background-position: -356px -130px;
      }
    }
  }
  &__title {
    width: 543px;
    height: 114px;
    margin: 0 auto 20px;
  }
  &__gift {
    width: 680px;
    margin: 0 auto;
  }
}