:root {
    font-size: 62.5%;
}

html {
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    &.is-popup {
        overflow: hidden;
    }
}

body {
    background-color: #000;
}
  
.wrap {
    max-width: 1920px;
    margin: 0 auto;
    background: #000 url('../images/site-bg.jpg') no-repeat center top;
    padding-bottom: 150px;
}

.rsp-img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
}

.rsp-h-img {
    width: auto;
    display: block;
    margin: 0 auto;
    height: 100%;
}

.inner {
    height: 100%;
    max-width: 768px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
}

.float-nav {
    position: fixed;
    left: 34px;
    top: 262px;
    z-index: 10;
    @include susy-breakpoint($s-320, $c8) {
        display: none;
    }
    &__btn-link {
        margin-bottom: 26px;
        width: 150px;
        height: 150px;
        display: block;
        background: transparent url('../images/left-nav-bg.png') no-repeat 0 0;
        background-size: 300px auto;
        &--account {
            background-position: 0 0;
            &:hover {
                background-position: -150px 0;
            }
        }
        &--download {
            background-position: 0 -150px;
            &:hover {
                background-position: -150px -150px;
            }
        }
        &--start {
            background-position: 0 -300px;
            &:hover {
                background-position: -150px -300px;
            }
        }
        &--event {
            background-position: 0 -450px;
            &:hover {
                background-position: -150px -450px;
            }
        }
    }
}

.btn-top {
    position: fixed;
    width: 185px;
    height: 126px;
    left: 50%;
    bottom: 160px;
    transform: translate3d(250%,0,0);
    @include susy-breakpoint($s-320, $c8) {
        display: none;
    }
}